<template>
  <div class="mqtt-message">
  </div>
</template>

<script>
import mqtt from "mqtt";
import { EventBus } from ".././modules/event-bus.js";

const host = location.hostname;

export default {
  data() {
    return {
      lastMessage : "",
      status : "Not connected",
      port: 8883,
      subscription: {
        topic: 'device/#',
        qos: 1,
      },
      publish: {
        topic: 'device/browser',
        qos: 1,
        payload: '{ "msg": "Hello, I am browser." }',
      },
      client: {
        connected: false,
      },
      subscribeSuccess: false,
    }
  },

  mounted(){
    return this.createConnection()
  },

  methods: {
    // Create connection
    createConnection() {
      // Connect string, and specify the connection method used through protocol
      const connectUrl = `mqtt://${host}:${this.port}`;
      console.log(connectUrl);
      try {
        console.log(connectUrl);
        this.client = mqtt.connect(connectUrl)
      }
      catch (error) {
        console.log('mqtt.connect error', error)
      }

      this.client.on('connect', () => {
        console.log('Connection succeeded!')
        this.status = 'Connection succeeded!'
      })

      this.client.on('error', error => {
        console.log('Connection failed', error)
      })

      this.client.on('message', (topic, message) => {
        let obj = JSON.parse(message);
        const dateTime = new Date().toLocaleTimeString("it-IT");
        this.lastMessage = `${dateTime} > Received ${message} on topic ${topic}`;
        EventBus.$emit(`${topic}`, obj.value);
      })

      // Add subscriptions to interested topic
      const { topic, qos } = this.subscription
      this.client.subscribe(topic, { qos }, (error) => {
        if (error) {
          console.log('Subscribe to topics error', error)
          return
        }
        this.subscribeSuccess = true
        console.log('Subscribed to topics of interest')
        })
    },
  },


  // This properties is accessible from parents
  props: {
    inMsg: String
  }
}
</script>