<template>
  <div>
    <div ref="modal" class="modal fade" :class="{show, 'd-block': active}"  tabindex="-1" role="dialog" >
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">Device configuration: {{ device }}</div>

          <div class="modal-body container" style="max-width: 95%;">

            <b-input-group prepend="Device Name" class="mt-3">
              <b-form-input id="deviceName" v-model="device"></b-form-input>
            </b-input-group>

            <b-input-group prepend="Organization" class="mt-3">
              <b-form-input id="organization" v-model="organization"></b-form-input>
            </b-input-group>
            <br>

            <label>Influx DB</label>
            <b-input-group prepend="Bucket" class="mt-3">
              <b-form-input  v-model="bucket"></b-form-input>
            </b-input-group>

            <b-input-group prepend="Token" class="mt-3">
              <b-form-input v-model="token"></b-form-input>
            </b-input-group>

            <!-- <b-input-group prepend="DB Host" class="mt-3">
              <b-form-input v-model="hostIP"></b-form-input>
            </b-input-group> -->

            <b-input-group prepend="DB Port" class="mt-3">
              <b-form-input type="number" v-model="DBPort" min="1" max="9999"></b-form-input>
            </b-input-group>

            <b-input-group prepend="MQTT Port" class="mt-3">
              <b-form-input type="number" v-model="MQTTPort" min="1" max="9999"></b-form-input>
            </b-input-group>
            <br>

            <label for="inputTime">Sensor setup</label>
            <b-input-group prepend="Acquisition time" class="mt-3">
              <b-form-input type="range" v-model="aTime" min="100" max="600" step="100" id="inputTime"></b-form-input>
              <div style="margin-top:5px; margin-left:10px">{{ aTime }}</div>
            </b-input-group>

            <b-input-group prepend="Amplifier gain" class="mt-3">
              <b-form-select v-model="aGain" :options="gainOptions" class="inputBox"></b-form-select>
              <div style="margin-top:5px; margin-left:10px">{{ aGain }}</div>
            </b-input-group>

            <b-input-group prepend="Persistent interrupt" class="mt-3">
              <b-form-select v-model="pIntType" :options="pintOptions" class="inputBox"></b-form-select>
              <div style="margin-top:5px; margin-left:10px">{{ pIntType }}</div>
            </b-input-group>

            <b-input-group prepend="LOW threshold" class="mt-3">
              <b-form-input type="number" v-model="pIntLowThr" min="0" max="65535"></b-form-input>
            </b-input-group>

            <b-input-group prepend="HIGH threshold" class="mt-3">
              <b-form-input type="number" v-model="pIntHighThr" min="0" max="65535"></b-form-input>
            </b-input-group>

            <b-input-group prepend="Non persistent interrupt (LOW)" class="mt-3">
              <b-form-input type="number" v-model="npIntLowThr" min="0" max="65535"></b-form-input>
            </b-input-group>

            <b-input-group prepend="Non persistent interrupt (HIGH)" class="mt-3">
              <b-form-input type="number" v-model="npIntHighThr" min="0" max="65535"></b-form-input>
            </b-input-group>

            <div class="modal-footer">
              <b-button variant="outline-primary" data-dismiss="modal" @click="toggleModal">Cancel</b-button>
              <b-button variant="outline-primary"  @click="saveConfig">Save configuration</b-button>
            </div>

          </div>
        </div>
      </div>
    </div>
    <div v-if="active" class="modal-backdrop fade show"></div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      active: false,
      show: false,

      device: 'Device',
      organization: 'work',
      bucket: 'measures',
      token: 'token',
      hostIP: '190.100.x.x',
      DBPort: 8086,
      MQTTPort: 1883,

      aTime: 10,
      aGain: 0,
      gainOptions: [
        { value: null, text: 'Please select an option' },
        { value: 0, text: 'LOW gain' },
        { value: 1, text: 'MEDIUM gain' },
        { value: 2, text: 'HIGH gain' },
        { value: 3, text: 'MAX gain' },
      ],
      pintOptions: [
        { value: null, text: 'Please select an option' },
        { value: 0, text: 'Every ALS cycle generate interrupt' },
        { value: 1, text: 'Any value outside of threshold range' },
        { value: 2, text: '2 consecutive values out of range' },
        { value: 3, text: '3 consecutive values out of range' },
        { value: 4, text: '5 consecutive values out of range' },
        { value: 5, text: '10 consecutive values out of range' },
        { value: 6, text: '15 consecutive values out of range' },
        { value: 7, text: '20 consecutive values out of range' },
        { value: 8, text: '25 consecutive values out of range' },
        { value: 9, text: '30 consecutive values out of range' },
        { value: 10, text: '35 consecutive values out of range' },
        { value: 11, text: '40 consecutive values out of range' },
        { value: 12, text: '45 consecutive values out of range' },
        { value: 13, text: '50 consecutive values out of range' },
        { value: 14, text: '55 consecutive values out of range' },
        { value: 15, text: '60 consecutive values out of range' },
      ],
      pIntType: 100,
      pIntLowThr: 50,
      pIntHighThr: 50,
      npIntLowThr: 100,
      npIntHighThr: 100,
    };
  },

  methods: {
    setDevice(d){
      this.device = d;
      console.log(d);
    },
    toggleModal() {
      const body = document.querySelector("body");
      this.active = !this.active;
      if (this.active) {
        this.loadConfig();
        body.classList.add("modal-open");
      }
      else {
        body.classList.remove("modal-open");
      }
      setTimeout(() => (this.show = !this.show), 10);
    },

    async loadConfig() {
      try {
        const response = await axios
        .post(`http://${location.hostname}/getConfig`, {
          device: this.device
        });
        const obj = response.data;
        this.device = obj["device"];
        this.bucket = obj["bucket"];
        this.organization = obj["organization"];
        this.token = obj["token"];
        this.hostIP = obj["hostIP"];
        this.DBPort = obj["DBPort"];
        this.MQTTPort = obj["MQTTPort"];
        this.aGain = obj["aGain"];
        this.aTime = obj["aTime"];
        this.pIntType = obj["pIntType"];
        this.pIntLowThr = obj["pIntLowThr"];
        this.pIntHighThr = obj["pIntHighThr"];
        this.npIntLowThr = obj["npIntLowThr"];
        this.npIntHighThr = obj["npIntHighThr"];
        console.log(obj);
      }
      catch (e) {
        console.log(e);
      }
    },

    async saveConfig() {
      try {
        const response = await axios
        .post(`http://${location.hostname}/setConfig`, {
          device: this.device,
          aTime: parseInt(this.aTime),
          aGain: parseInt(this.aGain),
          pIntType: parseInt(this.pIntType),
          pIntLowThr: parseInt(this.pIntLowThr),
          pIntHighThr: parseInt(this.pIntHighThr),
          npIntLowThr: parseInt(this.npIntLowThr),
          npIntHighThr: parseInt(this.npIntHighThr),
        });
        console.log(response);
      }
      catch (e) {
        console.log(e);
      }
    },

  }
};
</script>


<style scoped>
.btn{
  margin-right: 10px;
  margin-top: 40px;
  width: 160px;
}
.container{
  padding: 20px;
}

#inputTime{
  margin-left: 15px;
  width: 50%;
}

.inputBox{
    padding: 0.375rem 0.75rem;
    width: 65%;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
}

</style>

