<template>
  <div class="box">
    <div>
      <highcharts
      :options="chartOptions"
      ref="gauge_nmi"
      ></highcharts>
    </div>
  </div>
</template>

<script>
import { Chart } from "highcharts-vue";
import Highcharts from "highcharts";
import hcMore from "highcharts/highcharts-more";
import { EventBus } from ".././modules/event-bus.js";

hcMore(Highcharts);

export default {
  name: "voltage",
  props: {
    gaugeTitle: String,
    device: String,
  },
  components: {
    highcharts: Chart,
  },
  created(){
    this.topicName = `device/${this.device}/adc_value`;
    const channel = this.device.split('_')[1];
    this.chartOptions.yAxis[0].title.text = `<span style="font-size:12px">NMI - ${channel}</span>`;
    this.chartOptions.title.text = this.gaugeTitle;
  },
  mounted() {
    EventBus.$on(this.topicName, (val) => {
      const obj = JSON.parse(val);
      this.chartOptions.series[0].data = [obj];
    });
  },
  data() {
    return {
      topicName : String,
      chartOptions: {
        chart: {
          type: 'gauge',
          plotBorderWidth: 1,
          plotBackgroundColor: {
            linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
            stops: [
              [0.1, '#FFFFFF'],
              [1, '#FFF4C6']
            ]
          },
          plotBackgroundImage: null,
          plotShadow: true,
          height: 270,
          width: 300,
        },
        title: {
          text: 'Output Voltage Level'
        },
        pane: [{
          startAngle: -90,
          endAngle: 90,
          background: null,
          center: ['50%', '60%'],
        }],
        exporting: {
          enabled: false
        },
        tooltip: {
          enabled: false
        },
        yAxis: [{
          min: 18,
          max: 24,
          minorTickPosition: 'outside',
          tickPosition: 'outside',
          minorTickInterval: 'auto',
          minorTickLength: 7,
          tickPixelInterval: 30,
          tickWidth: 3,
          tickColor: '#666',
          labels: {
            step: 1,
            rotation: 'auto',
            distance: 16,
          },
          plotBands: [{
            from: 19,
            to: 21,
            color: '#C0FF16',
            innerRadius: '90%',
            outerRadius: '100%',
          },
          {
            from: 21,
            to: 22,
            color: '#FFFF16',
            innerRadius: '90%',
            outerRadius: '100%',
          },
          {
            from: 22,
            to: 24,
            color: '#C0231688',
            innerRadius: '10%',
            outerRadius: '100%',
          }],
          pane: 0,
          title: {
            text: '<span style="font-size:12px">Title</span>',
            y: 110,
          },
        }],
        plotOptions: {
          gauge: {
            dial: {
              radius: '100%',
            },
          },
        },
        series: [{
          name: 'Voltage setup',
          data: [20],
          yAxis: 0,
        }]
      },
    };
  },
};
</script>
