<template>
  <div>
  <highcharts
    :options="chartOptions"
    :updateArgs="updateArgs"
    ref="chart_nmi_left"
    :constructor-type="'stockChart'"
  />
  </div>
</template>

<script>
import axios from 'axios';
import Highcharts from 'highcharts';
import stockInit from 'highcharts/modules/stock';
import exporting from 'highcharts/modules/exporting';
import exportingData from 'highcharts/modules/export-data';
stockInit(Highcharts);
exporting(Highcharts);
exportingData(Highcharts);

export default {
  props: {
      device: String,
  },
  methods: {
    async getData() {
      const Chart = this.$refs.chart_nmi_left.chart;
      axios
        .get(`http://${this.host}/getData?device=${this.device}&range=${this.dataRange}&window=10`)
        .then(response => {
            response.data.forEach(rec => {
            const point = [new Date(rec._time).getTime(), rec._value];
            Chart.series[rec.table].addPoint(point, false, false);
          });
          Chart.redraw();
          this.dataRange = '-10s';
          setTimeout(this.getData, 10000);
        });
    },
  },
  created() {
    const channel = this.device.split('_')[1];
    this.chartOptions.title.text = `Light Intensity NMI-${channel}`;
  },
  mounted(){
    this.getData();
  },
  data() {
    return {
      host: location.hostname,
      maxPoints: 20,
      dataRange: '-1d',
      updateArgs: [true, true, { duration: 100 }],
      chartOptions: {
        chart: {
          zoomType: 'x',
          height: 380,
        },
        title: {
          text: '',
          align: 'right',
          x: -100,
          y: 20,
          floating: true,
        },
        xAxis: {
          type: 'datetime',
        },
        legend: {
          enabled: false,
        },
        tooltip: {
            pointFormat: '{series.name}: <b>{point.y:.2f}</b><br/>',
            shared: true,
        },
        rangeSelector: {
          inputEnabled: false,
          selected: 3,
          buttons: [
            { count: 5, type: 'minute', text: '5 m' },
            { count: 15, type: 'minute', text: '15 m' },
            { count: 60, type: 'minute', text: '60 m' },
            { count: 1140, type: 'minute', text: '1 d' },
            { type: 'all', text: 'all' },
          ],
        },
        navigator: {
          margin: 5,
          adaptToUpdatedData: true,
          height: 20,
          outlineWidth: 0,
          maskFill: 'rgba(0, 0, 0, 0.2)',
          series: {
            type: 'line',
            color: '#bbb',
          }
        },
        scrollbar: {
          enabled: false
        },
        plotOptions: {
          area: {
            lineWidth: 1,
            fillColor: {
              linearGradient: { x1: 0, y1: 0, x2: 0,  y2: 1},
              stops: [
                [0, Highcharts.getOptions().colors[4]],
                [1, Highcharts.color(Highcharts.getOptions().colors[1]).setOpacity(0).get('rgba')]
              ]
            },
          },
        },
        series: [
          {
            color: 'red',
            type: 'area',
            name: 'Infrared light intensity',
            data: [],
          },
          {
            color: 'blue',
            type: 'area',
            name: 'Visible light intensity',
            data: [],
          },
        ]
      },
    };
  },
};
</script>

<style>
  .highcharts-credits {
    display: none;
  }
</style>