<template>
  <div>
    <label for='volt-control'>{{sliderLabel}}</label>
    <div class="input-group mb-3" id='volt-control'>
      <button class="btn btn-outline-secondary sm plus-minus" @click="minus">
        <svg xmlns='http://www.w3.org/2000/svg' width='20' height='20' fill='currentColor' class='bi bi-dash-square' viewBox='0 0 16 16'>
        <path d='M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z'/>
        <path d='M4 8a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7A.5.5 0 0 1 4 8z'/>
        </svg>
      </button>
      <div class="range-wrap">
        <input id="range" type="range" ref="mySlider" min="0" max="100" step="1" v-model="value" @change="storeData">
        <div class="range-value" v-bind:style="{left: leftTip + '%' }" id="rangeV">
          <span>{{ value }}</span>
        </div>
      </div>
      <button class="btn btn-outline-secondary sm plus-minus" @click="plus">
        <svg xmlns='http://www.w3.org/2000/svg' width='20' height='20' fill='currentColor' class='bi bi-plus-square' viewBox='0 0 16 16'>
        <path d='M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z'/>
        <path d='M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z'/>
        </svg>
      </button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  props: ['device', 'sliderLabel'],
  data() {
    return {
      leftTip: 20,
      value: 24,
    };
  },
  mounted() {
    this.getData();
  },
  watch: {
    // Whenever value changes, this function will run
    value() {
      this.value = Math.max(0, this.value);
      this.value = Math.min(100, this.value);
      const perc = Number((this.value) * 100 / (100));
      this.leftTip = perc + (3 - perc * 0.06);
      this.storeData();
    },
  },
  methods: {
    plus() {
      this.value += 1;
    },
    minus() {
      this.value -= 1;
    },
    async getData() {
      try {
        const response = await axios.get(`http://${location.hostname}/getParam?device=${this.device}&param=dac_output`);
        this.value = Number(response.data[0]._value);
      }
      catch (e) {
        console.log(e);
      }
    },
    async storeData() {
      try {
        const response = await axios.get(`http://${location.hostname}/setParam?device=${this.device}&param=dac_output&value=${this.value}`);
        console.log(response);
      }
      catch (e) {
        console.log(e);
      }
    },
  },
};
</script>

